/*
  Layout
*/

@use "variables" as *;
@use "config" as *;

/* layout ------------------------------------------------- */
.wrap {
  position: relative;
  overflow: hidden;
  //min-height: calc(100dvh - 312px - 50px - 80px);
}

.container {
  min-height: 800px;
  margin: 0 auto;
  max-width: none !important;
  padding-top: calc(102px + 32px);
}

.inner {
  max-width: $contents-size;
  margin: 0 auto;
}

@media (max-width: $breakpoint-xlarge) {
  .container {
    padding-top: calc(66px + 32px);
  }

  //.wrap {
  //  min-height: calc(100dvh - 301px - 50px - 81px);
  //}

  .inner {
    padding: 0 15px;
  }
}

@media (max-width: $breakpoint-medium) {
  .wrap {
    min-height: auto;
  }
}
/* -------------------------------------------------------- */

/* header ------------------------------------------------- */
.skip-nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 500;

  a {
    display: block;
    width: 100%;
    height: 0;
    background-color: #555;
    text-align: center;
    color: $white;
    font-size: 0;
    padding: 0;

    &:focus {
      line-height: 30px;
      font-size: 16px;
      height: 30px;
      outline: none;
    }
  }
}

.mast-head {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: $bg-hover;
  z-index: 10;

  .inner {
    display: flex;
    position: relative;
    gap: 4px 16px;
    min-height: 32px;
    align-items: center;
    justify-content: flex-start;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 4px 0;
  }

  .nuri-txt {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: row;
    gap: 8px;
    font-size: 15px;
    white-space: nowrap;

    &::before {
      content: '';
      display: inline-flex;
      width: 24px;
      height: 24px;
      background-image: url(../images/common/ico-flag.svg);
      background-size: cover;
      background-repeat: no-repeat;
    }
  }
}

.header {
  position: fixed;
  top: 32px;
  left: 0;
  width: 100%;
  border-bottom: 1px solid #d8d8d8;
  background-color: $white;
  z-index: 10;

  .utility {
    height: 40px;
    display: flex;
    justify-content: flex-end;

    .list {
      display: flex;
      align-items: center;

      .item {
        display: flex;
        align-items: center;

        > .item-link {
          display: inline-flex;
          align-items: center;
          justify-content: center;
          font-size: 15px;
          gap: 2px;
          padding: 2px 5px;
          transition: 0.4s ease-in-out;
          border-radius: 6px;
        }

        + .item {
          &::before {
            content: '';
            display: inline-flex;
            width: 1px;
            height: 16px;
            background-color: #d8d8d8;
            margin: 0 8px;
          }
        }
      }
    }
  }

  .btn-menu {
    position: relative;
    width: 20px;
    height: 20px;

    span {
      position: absolute;
      display: block;
      width: 16px;
      height: 2px;
      transition: all 0.2s;
      background-color: $gray-222;
      left: 2px;

      &:nth-child(1) {
        top: 2px;
      }

      &:nth-child(2) {
        top: 9px;
      }

      &:nth-child(3) {
        top: 16px;
      }
    }
  }

  .btn-search {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 20px;
    height: 20px;
  }

  .branding {
    display: flex;
    align-items: center;
    padding-bottom: 16px;

    .logo {
      flex-shrink: 0;
      width: 246px;
      height: 46px;
      margin-right: 110px;

      a {
        display: block;
        width: 100%;
        height: 100%;

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }

    .gnb-menu {
      display: flex;
      align-items: center;

      .menu-link {
        font-size: 24px;
        font-weight: bold;
        transition: .2s;

        &:hover {
          color: #003675;
        }
      }
    }

    .main-menu {
      margin-left: auto;

      .gnb-menu {
        display: flex;
        align-items: center;
        gap: 80px;

        .menu-link {
          font-size: 24px;
          white-space: nowrap;
          font-weight: bold;
        }
      }
    }

    .actions {
      display: flex;
      align-items: center;
      gap: 16px;
      margin-left: 110px;

      .action-item {
        display: flex;
        justify-content: center;
        align-items: center;
        transition: .2s;
        border-radius: 6px;
        padding: 2px;

        &:hover {
          background-color: #EDF1F5;
        }

        &:active {
          background-color: #CDD7E4;
        }
      }

      .txt-link {
        font-size: 16px;
        font-weight: 500;
        color: $gray-444;
      }
    }
  }
}

@media (max-width: $breakpoint-xlarge) {
  .mast-head {
    .inner {
      padding: 4px 15px;
    }

    .nuri-txt {
      gap: 4px;
      font-size: 15px;
    }
  }

  .header {
    .utility {
      display: none;
    }

    .branding {
      height: 65px;
      padding-bottom: 0;
      gap: 10px;

      .logo {
        width: 172px;
        height: 32.3px;
        margin-right: 0;
      }

      .main-menu {
        display: none;
      }

      .actions {
        margin-left: auto;
        gap: 10px;
      }
    }

    .btn-search {
      width: auto;
      height: auto;
      display: flex;
      align-items: center;
      gap: 5px;

      .ico {
        flex-shrink: 0;
      }

      em {
        display: inline-flex;
        font-size: 15px;
        line-height: 20px;
        font-weight: 500;
        white-space: nowrap;
        color: $gray-444;
      }
    }

    .action-item.show-mo {
      display: flex !important;
    }

    .btn-menu {
      width: auto;
      padding-right: 72px;

      em {
        display: inline-flex;
        font-size: 15px;
        line-height: 20px;
        font-weight: 500;
        color: $gray-444;
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }
}

@media (max-width: $breakpoint-small) {
  .header {
    .branding {
      .actions {
        gap: 10px;
      }
    }

    .btn-search {
      em {
        display: none !important;
      }
    }

    .btn-menu {
      padding-right: 20px;

      em {
        display: none;
      }
    }
  }
}
/* -------------------------------------------------------- */

/* footer ------------------------------------------------- */
.footer {
  background-color: #f0f0f0;
  padding: 40px 0;
  border-top: 1px solid #d8d8d8;
  margin-top: 100px;

  a {
    &:focus {
      outline: none;
      border: 0;
    }

    &:focus-visible {
      outline: 2px solid $black;
      outline-offset: 2px;
    }
  }

  .footer-dropdown {
    position: relative;
    margin-bottom: 30px;

    .btn-select {
      display: flex;
      width: 230px;
      height: 45px;
      font-weight: 500;
      font-size: 17px;
      letter-spacing: -.85px;
      border-bottom: 2px solid $gray-222;
      padding: 9px 15px 9px 0;
      align-items: center;
      justify-content: space-between;

      &::after {
        content: '';
        display: block;
        width: 13px;
        height: 7px;
        background: url(../images/common/footer-select.png) no-repeat center;
        box-sizing: border-box;
        -webkit-text-size-adjust: 100%;
        -webkit-font-smoothing: antialiased;
      }
    }

    .dropdown-list {
      position: absolute;
      right: 0;
      bottom: 20px;
      width: 100%;
      max-height: 200px;
      padding: 10px 15px;
      background: rgba(255, 255, 255, 1);
      overflow-y: auto;
      transition: 0.3s;
      opacity: 0;
      visibility: hidden;

      a {
        display: block;
        padding: 8px 10px;
        font-weight: bold;
        font-size: 15px;
        border-bottom: 1px solid $gray-eee;
        transition: color .2s;

        &:hover {
          color: #008be5;
        }
      }

      li:last-child {
        a {
          border-bottom: 0;
        }
      }
    }

    .btn-select-close {
      display: none;
      width: 230px;
      height: 45px;
      font-weight: 500;
      font-size: 17px;
      letter-spacing: -.85px;
      border: 2px solid #697077;
      padding: 5px 15px;
      align-items: center;
      justify-content: space-between;
    }

    &.active {
      .btn-select {
        display: none;
      }

      .dropdown-list {
        bottom: 45px;
        opacity: 1;
        visibility: visible;
      }

      .btn-select-close {
        display: flex;
        color: #008be5;
        border-color: #008be5;

        &::after {
          content: '';
          box-sizing: border-box;
          -webkit-text-size-adjust: 100%;
          -webkit-font-smoothing: antialiased;
          display: block;
          width: 12px;
          height: 7px;
          background: url(../images/common/select-close.png) no-repeat center;
        }
      }
    }
  }

  .footer-info {
    .inner {
      display: flex;
      justify-content: space-between;
      gap: 20px;
      border-bottom: 1px solid #d8d8d8;
      padding-bottom: 40px;
    }

    .logo {
      width: 246px;
      height: 40px;
      margin-bottom: 37px;

      a {
        display: block;
        width: 100%;
        height: 100%;

        img {
          display: block;
          width: 100%;
        }
      }
    }

    .info-cs {
      .address {
        font-size: 17px;
        color: #1d1d1d;
        margin-bottom: 16px;
      }

      .key-info {
        font-size: 17px;
        color: #1d1d1d;
      }
    }

    .f-link {
      .links {
        display: flex;
        flex-direction: column;
        gap: 16px;
        margin-bottom: 40px;

        .link {
          display: inline-flex;
          font-size: 17px;
          font-weight: bold;
          color: #1d1d1d;
          line-height: 20px;
          padding: 2px 0;

          &::after {
            content: '';
            display: inline-flex;
            background-image: url(../images/common/ico-footer-arrow.svg);
            width: 20px;
            height: 20px;
            background-size: cover;
            margin-left: 4px;
          }
        }
      }

      .sns-links {
        display: flex;
        gap: 8px;

        .link {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 40px;
          height: 40px;
          border: 1px solid #E4E4E4;
          border-radius: 100%;
          background-color: $white;
        }
      }
    }
  }

  .footer-links {
    padding-top: 15px;

    .link-content {
      display: flex;
      justify-content: space-between;
      margin-bottom: 10px;
    }

    .info-links {
      display: flex;
      flex-wrap: wrap;
      gap: 20px 30px;

      .link {
        font-size: 17px;
        line-height: 24px;
        letter-spacing: 0.6px;

        &.link-highlight {
          font-weight: bold;
          color: #003675;
        }
      }
    }

    .copyright {
      font-size: 15px;
      color: #555555;
      margin-bottom: 35px;
    }

    .nuri {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      background-color: $white;
      padding: 8px 16px;
      border-radius: 8px;
      gap: 8px;
      color: #1D1D1D;
      font-size: 15px;

      img {
        width: 74px;
        height: 24px;
      }
    }
  }
}

@media (max-width: $breakpoint-xlarge) {
  .footer {
    margin-top: 70px;

    .footer-info {
      .inner {
        flex-direction: column;
        border-bottom: 0;
        padding-bottom: 0;
      }

      .logo {
        width: 172px;
        height: 32px;
        margin-bottom: 25px;
      }

      .info-cs {
        .address {
          margin-bottom: 30px;
        }
      }

      .f-link {
        padding-bottom: 40px;
        border-bottom: 1px solid #d8d8d8;

        .links {
          margin-bottom: 30px;
        }
      }
    }

    .footer-links {
      padding-top: 26px;

      .info-links {
        margin-bottom: 30px;
      }

      .copyright {
        margin-bottom: 30px;
      }
    }
  }
}
/* -------------------------------------------------------- */

/* family site -------------------------------------------- */
.family-site {
  padding: 10px 0;
  border-top: 1px solid #dfdfdf;
  margin-top: 30px;

  .swiper {
    max-width: 1200px;
    margin: 0 auto;

    .swiper-slide {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 60px;

      a {
        display: flex;
        width: 100%;
        height: 100%;
        align-items: center;
        justify-content: center;
      }
    }
  }

  .btn-prev, .btn-next {
    width: 20px;
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 2;

    &.swiper-button-disabled {
      opacity: 0.3;
    }
  }

  .btn-prev {
    left: 15px;
  }

  .btn-next {
    right: 15px;
  }
}

@media (min-width: $breakpoint-xlarge) {
  .family-site {
    .btn-prev, .btn-next {
      visibility: hidden;
      cursor: default;
    }
  }
}
/* -------------------------------------------------------- */

/* etc ---------------------------------------------------- */
.btn-chatbot {
  position: fixed;
  bottom: 22%;
  transform: translateY(-50%);
  right: 20px;
  width: 72px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 10;
  text-align: center;

  .txt {
    font-size: 15px;
    font-weight: bold;
    line-height: 14px;
    letter-spacing: -0.7px;
    text-align: center;
    color: #0e75dc;
    margin-top: 8px;
  }
}

@media (max-width: $breakpoint-medium) {
  .btn-chatbot {
    bottom: 3%;

    .txt {
      display: none;
    }
  }
}
