/*
  Accordion
*/

@use "variables" as *;

.accordion {
  border-top: 1px solid #cdd1d5;

  .accordion-item {
    padding: 4px 0;
    transition: 0.4s ease-in-out;
    border-bottom: 1px solid #cdd1d5;

    .btn-accordion {
      position: relative;
      width: 100%;
      padding: 20px 40px 20px 0;
      background-color: $white;
      text-align: left;
      font-size: 19px;
      font-weight: bold;
      border-radius: 10px;
      
      &:hover {
        background-color: $bg-hover;
      }

      &:active {
        background-color: $bg-active;
      }

      &::after {
        content: '';
        position: absolute;
        top: 50%;
        right: 0;
        width: 24px;
        height: 24px;
        background-image: url(../images/common/ico-arrodion.svg);
        transition: 0.4s ease-in-out;
        transform: translateY(-50%) rotate(0deg);
      }

      &.active {
        &:focus {
        }

        &::after {
          transform: translateY(-50%) rotate(180deg);
        }
      }
    }

    .accordion-collapse {
      max-height: 0;
      overflow: hidden;
      transition: all .5s cubic-bezier(0, 1, 0, 1);
      visibility: hidden;
    }

    .accordion-body {
      padding: 20px;
    }

    &.active {
      .accordion-collapse {
        height: auto;
        max-height: 100vh;
        transition: all 1.5s ease-in-out;
        visibility: visible;
      }
    }
  }
}