/*
   
*/
@use "variables" as *;
@use "config" as *;

.card-list-wrap {
  padding-right: 50px;

  .btn-circle-prev, .btn-circle-next {
    position: absolute;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2);
    border: solid 1px $gray-ccc;
    background-color: $white;
    top: 50%;
    border-radius: 100%;
    transform: translateY(-50%);
    z-index: 2;

    &.swiper-button-disabled {
      display: none;
      //opacity: .4;
    }
  }

  .btn-circle-prev {
    left: -75px;
    padding-right: 2px;
  }

  .btn-circle-next {
    right: -25px;
    padding-left: 2px;
  }

  .card-list {
    display: flex;
    gap: 30px;
    word-break: keep-all;

    .card-link {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 1;

      &:focus, &:focus-visible {
        outline-offset: -4px;
        outline: 2px solid $gray-222;
        border-radius: 10px;
      }
    }

    .card-item {
      position: relative;
      min-width: 0;
      flex: 1;
      display: flex;
      flex-direction: column;
      gap: 20px;

      .card-thumb {
        position: relative;
        width: 100%;
        border-radius: 10px;
        overflow: hidden;

        &::before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          border: 1px solid $gray-ccc;
          border-radius: 10px;
          width: 100%;
          height: 100%;
          z-index: 0;
        }

        img {
          display: block;
          width: 100%;
          height: auto;
        }
      }

      .card-category {
        display: flex;
        align-items: center;
        margin-bottom: 10px;

        .badge {
          height: 34px;
          min-width: 80px;
          padding: 0 15px;
          border-radius: 2px;
          font-size: 16px;
          font-weight: bold;
          line-height: 34px;
          border: 0;
          letter-spacing: -0.8px;
          color: $white;

          &.current {
            background-color: $point;
          }

          &.upcoming {
            background-color: #228738;
          }

          &.done {
            background-color: $gray-222;
          }

          &.download {
            z-index: 2;
            background-color: #003675;
            cursor: pointer;
          }
        }

        .detail {
          display: flex;
          align-items: center;
          //line-height: 34px;
          font-size: 16px;
          font-weight: bold;
          letter-spacing: -0.8px;

          &::before {
            content: '';
            display: inline-flex;
            width: 50px;
            height: 2px;
            background-color: $gray-222;
            margin: 0 10px;
          }
        }
      }

      .card-title {
        font-size: 19px;
        font-weight: bold;
        line-height: 26px;
        letter-spacing: -.95px;
        //min-height: 52px;
        //display: -webkit-box;
        //overflow: hidden;
        //text-overflow: ellipsis;
        //-webkit-box-orient: vertical;
        //-webkit-line-clamp: 2;
        margin-bottom: 10px;
      }

      .card-meta {
        display: flex;
        gap: 0;
        flex-direction: column;

        div {
          display: flex;
          align-items: flex-start;
          font-size: 15px;
          //line-height: 20px;
          gap: 5px 10px;
        }

        dt {
          font-weight: bold;
          color: $gray-222;
        }

        dd {
          //line-height: 20px;
          color: $gray-444;
        }
      }
    }
  }

  &.card-thumb-list-wrap {
    .btn-circle-prev, .btn-circle-next {
      transform: translateY(calc(-50% - 40px));
    }

    .btn-circle-prev {
      left: -25px;
    }

    .btn-circle-next {
      right: -25px;
    }

    .card-list {
      .card-item {
        .card-title {
          font-size: 17px;
          font-weight: 500;
          letter-spacing: -.85px;
          //line-height: 24px;
          margin-bottom: 0;
        }

        .card-thumb {
          aspect-ratio: 290 / 163;
          overflow: hidden;
          border-radius: 10px;
        }
      }
    }
  }
}

@media (max-width: $breakpoint-xlarge) {
  .card-list-wrap {
    padding: 0 50px;

    .btn-circle-prev {
      left: -10px;
    }

    .btn-circle-next {
      right: -10px;
    }

    &.card-thumb-list-wrap {
      .swiper {
        flex-direction: column;
      }

      .swiper-wrapper {
        position: relative;
        gap: 30px;
        flex-direction: column;
        padding: 0;

        .swiper-slide {
          &.is-hidden {
            display: none;
          }
        }
      }

      .btn-circle-prev, .btn-circle-next {
        display: none;
      }

      .card-list {
        .card-item {
          .card-link {
            gap: 15px;
          }

          .card-title {
            min-height: auto;
          }
        }
      }

      .btn-square-more {
        display: flex;
        margin-top: 30px;
      }
    }
  }
}

.result-group {
  display: flex;
  flex-direction: column;
  gap: 12px;

  // 
  + .result-group {
    border-top: 1px solid $gray-eee;
    margin-top: 40px;
    padding-top: 40px;
  }

  .result-group-head {
    display: flex;
    align-items: center;

    .title {
      font-size: 24px;
      letter-spacing: -1.2px;
      font-weight: bold;
    }

    .count {
      font-size: 20px;
      font-weight: 500;
      margin-left: 5px;
      letter-spacing: -1px;
    }

    .btn-more {
      margin-left: auto;
      font-size: 15px;
      text-decoration: underline;
      font-weight: 500;
      color: $gray-666;
      letter-spacing: -.7px;
    }
  }

  .card-list-wrap {
    padding-right: 0;

    .card-item {
      gap: 15px;

      .card-category {
        .badge {
          height: 30px;
          line-height: 30px;
          padding: 0 8px;
          min-width: auto;
          font-size: 15px;
        }

        .detail {
          font-size: 15px;
          letter-spacing: -1.5px;
          white-space: nowrap;

          &::before {
            width: 30px;
          }
        }
      }

      .card-title {
        font-size: 20px;
        //line-height: 26px;
        letter-spacing: -1.1px;
      }

      .card-meta {
        div {
          font-size: 15px;
        }
      }
    }
  }

  .sub-info {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    font-size: 15px;
    letter-spacing: -.7px;
    color: $gray-666;

    &-item {
      position: relative;
      padding-right: 10px;
      margin-right: 10px;

      &::after {
        content: '';
        position: absolute;
        right: 0;
        top: 0;
        color: $gray-ccc;
      }

      &:last-child {
        padding-right: 0;
        margin-right: 0;

        &::after {
          display: none;
        }
      }
    }
  }

  .record-info {
    display: flex;
    flex-wrap: wrap;
    gap: 5px 20px;

    .record-info-item {
      position: relative;
      display: flex;
      gap: 4px;
      align-items: center;
      font-size: 17px;

      + .record-info-item {
        &::before {
          content: '';
          position: absolute;
          width: 1px;
          height: 12px;
          top: 7px;
          left: -10px;
          background-color: #dfdfdf;
        }
      }

      dt {
        color: $gray-666;
      }

      dd {
        font-weight: 500;
      }
    }
  }

  //  
  .person-list {
    display: grid;
    grid-template-columns: repeat(5, minmax(0, 1fr));
    gap: 30px 50px;

    .item {
      min-width: 0;

      .card {
        display: block;
        text-align: center;

        .thumb {
          position: relative;
          overflow: hidden;
          border: 1px solid #dfdfdf;
          border-radius: 10px;
          aspect-ratio: 3 / 4;

          img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: .2s;
          }
        }

        .overlay {
          position: absolute;
          inset: 0;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          background: rgba(10, 77, 203, 0.8);
          color: $white;
          text-align: center;
          transform: translateY(calc(100% + 1px));
          transition: transform 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);

          .name {
            font-size: 17px;
            font-weight: bold;
          }

          .desc {
            position: relative;
            font-size: 15px;
            //line-height: 20px;
            margin-top: 50px;

            &::before {
              content: '';
              position: absolute;
              top: -40px;
              left: 50%;
              transform: translateX(-50%);
              width: 2px;
              height: 30px;
              background-color: $white;
            }
          }
        }

        .info {
          margin-top: 4px;

          .name {
            width: 100%;
            display: block;
            font-size: 17px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
          }
        }

        &:hover {
          .thumb {
            img {
              transform: scale(1.2);
            }

            .overlay {
              transform: translateY(0);
            }
          }
        }
      }
    }
  }

  //  
  .history-list {
    display: flex;
    flex-direction: column;
    gap: 30px;

    .item {
      position: relative;
      display: flex;
      gap: 40px;

      .link {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
      }

      .thumb {
        flex-shrink: 0;
        width: 220px;
        height: 165px;
        overflow: hidden;
        border-radius: 10px;
        border: 1px solid $gray-ccc;

        img {
          display: block;
          width: 100%;
          height: 100%;
        }
      }

      .info {
        width: calc(100% - 220px - 40px);

        .title {
          font-size: 19px;
          font-weight: bold;
          margin-bottom: 10px;
        }

        .meta {
          display: grid;
          grid-template-columns: repeat(4, 1fr);
          gap: 20px 5px;
          margin-bottom: 10px;

          .col {
            position: relative;
            padding-left: 10px;

            &::before {
              content: '';
              position: absolute;
              top: 0;
              left: 0;
              width: 2px;
              height: 100%;
              background: $gray-222;
            }
          }

          .meta-term {
            font-size: 15px;
            font-weight: 500;
            //line-height: 14px;
            color: $gray-666;
          }

          .meta-desc {
            font-size: 17px;
            font-weight: 500;
          }
        }

        .desc {
          display: -webkit-box;
          overflow: hidden;
          text-overflow: ellipsis;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 3;
          font-size: 16px;
          //line-height: 24px;
          letter-spacing: -0.8px;
          color: $gray-444;
        }
      }
    }
  }

  //  
  .video-list {
    display: grid;
    grid-template-columns: repeat(3, minmax(0, 1fr));
    gap: 27px 55px;

    .item {
      position: relative;
      min-width: 0;

      .link {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
      }

      .thumb {
        width: 100%;
        border: 1px solid $gray-ccc;
        border-radius: 10px;
        overflow: hidden;
        aspect-ratio: 16 / 9;
        margin-bottom: 4px;

        img {
          display: block;
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }

      .info {
        min-width: 0;

        .title {
          font-size: 17px;
          font-weight: 500;
          letter-spacing: -.85px;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
    }

    &.video-vertical-list {
      gap: 30px;
      grid-template-columns: repeat(1, minmax(0, 1fr));

      .item {
        display: flex;
        gap: 40px;

        .thumb {
          flex-shrink: 0;
          width: 260px;
          height: 146px;
          aspect-ratio: 260 / 146;
          margin-bottom: 0;
        }

        .info {
          min-width: 0;
          flex: 1;
        }

        .title {
          font-size: 20px;
          font-weight: bold;
          //line-height: 24px;
          margin-bottom: 8px;
          padding-top: 8px;
        }

        .desc {
          font-size: 16px;
          //line-height: 24px;
          font-weight: 400;
          color: $gray-444;
          margin-bottom: 5px;
        }

        .hashtag {
          display: flex;
          flex-wrap: wrap;
          font-size: 16px;
          //line-height: 24px;
          font-weight: 500;
          color: #1d56bc;
          gap: 10px;
        }

        .sub-info {
          .sub-info-item {
            padding-right: 0;

            &::after {
              content: none;
            }
          }
        }
      }
    }
  }

  //  
  .photo-list {
    display: grid;
    grid-template-columns: repeat(4, minmax(0, 1fr));
    gap: 25px 30px;

    .item {
      position: relative;
      min-width: 0;

      .link {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
      }

      .thumb {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: auto;
        overflow: hidden;
        border-radius: 10px;
        background-color: #f6f8f7;
        border: 1px solid #cadaee;
        aspect-ratio: 200 / 150;
        margin-bottom: 4px;

        img {
          display: block;
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }

      .info {
        min-width: 0;

        .title {
          font-size: 17px;
          letter-spacing: -0.85px;
          color: $gray-444;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
    }
  }

  //  
  .record-list {
    display: flex;
    flex-direction: column;
    gap: 20px;

    .item {
      position: relative;

      .link {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
      }

      .info {
        .title {
          font-size: 19px;
          font-weight: bold;
          margin-bottom: 7px;
        }
      }
    }
  }

  //  
  .book-list {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 30px;

    .link {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 1;
    }

    .item {
      position: relative;
      display: flex;
      gap: 40px;

      .thumb {
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        background-color: $white;
        border: 1px solid $gray-ccc;
        aspect-ratio: 100 / 146;
        height: auto;
        width: 100px;
        align-self: flex-start;
      }

      .info {
        min-width: 0;
        flex: 1;

        .title {
          font-size: 19px;
          font-weight: bold;
          letter-spacing: -.85px;
          margin-bottom: 6px;
        }

        .category {
          font-size: 17px;
          font-weight: 500;
          color: #dc2347;
          margin-bottom: 2px;
        }
      }
    }
  }
}

@media (max-width: $breakpoint-xlarge) {
  .result-group {
    .card-list-wrap {
      padding-left: 0;

      .card-list {
        display: grid;
        grid-template-columns: repeat(2, minmax(0, 1fr));
        gap: 30px 20px;
      }
    }

    .person-list {
      grid-template-columns: repeat(3, minmax(0, 1fr));
      gap: 30px 20px;
    }

    .video-list {
      grid-template-columns: repeat(2, minmax(0, 1fr));
      gap: 30px 20px;

      &.video-vertical-list {
        gap: 20px;

        .item {
          gap: 30px 20px;

          .thumb {
            width: 30%;
            height: auto;
            align-self: flex-start;
          }

          .title {
            padding-top: 0;
          }
        }
      }
    }

    .history-list {
      .item {
        gap: 20px;

        .thumb {
          width: 30%;
          height: auto;
          align-self: flex-start;
        }

        .info {
          width: auto;
          flex: 1;

          .meta {
            grid-template-columns: repeat(2, 1fr);
            gap: 20px 5px;
            margin-bottom: 10px;
          }
        }
      }
    }

    .photo-list {
      grid-template-columns: repeat(2, minmax(0, 1fr));
      gap: 30px 20px;
    }

    .book-list {
      .item {
        gap: 20px;
      }
    }
  }
}
