/*
  Icon
*/
@use "variables" as *;

.ico {
  display: inline-block;
  background-size: cover;
  background-repeat: no-repeat;
}

.ico-search {
  width: 20px;
  height: 20px;
  background-image: url(../images/common/ico-search.png);
}

.ico-toggle {
  width: 16px;
  height: 16px;
  background-image: url(../images/common/ico-toggle.svg);
}

.ico-go {
  width: 16px;
  height: 16px;
  background-image: url(../images/common/ico-go.svg);
}

.ico-plus {
  width: 10px;
  height: 10px;
  background-image: url(../images/main/ico-plus.png);
}

.ico-slider-prev-sm {
  width: 7px;
  height: 12px;
  background-image: url(../images/main/ico-slider-prev-sm.png);
}

.ico-slider-next-sm {
  width: 7px;
  height: 12px;
  background-image: url(../images/main/ico-slider-next-sm.png);
}

.ico-slider-paused {
  width: 8px;
  height: 10px;
  background-image: url(../images/main/ico-slider-paused.png);
}

.ico-slider-paused-white {
  width: 10px;
  height: 12px;
  background-image: url(../images/main/ico-slider-paused-white.png);
}

.ico-slider-prev {
  width: 10px;
  height: 18px;
  background-image: url(../images/main/ico-slider-prev.png);
}

.ico-slider-next {
  width: 10px;
  height: 18px;
  background-image: url(../images/main/ico-slider-next.png);
}

.ico-search-main {
  width: 24px;
  height: 24px;
  background-image: url(../images/main/ico-search-main.png);
}

.ico-prev-white {
  width: 9px;
  height: 16px;
  background-image: url(../images/main/ico-nav-prev-white.png);
}

.ico-next-white {
  width: 9px;
  height: 16px;
  background-image: url(../images/main/ico-nav-next-white.png);
}

.ico-slider-play {
  width: 6px;
  height: 10px;
  background-image: url(../images/main/ico-slider-play.png);
}

.ico-slider-play-white {
  width: 10px;
  height: 12px;
  background-image: url(../images/main/ico-play-white.png);
}

.ico-home {
  width: 12px;
  height: 12px;
  background-image: url(../images/common/ico-home.png);
}

.ico-del {
  width: 13px;
  height: 13px;
  background-image: url(../images/common/ico-del.png);
}

.ico-verified {
  width: 21px;
  height: 21px;
  background-image: url(../images/common/ico-verified.png);
}

.ico-external {
  width: 14px;
  height: 14px;
  background-image: url(../images/common/ico-external-black.png);
}

*:hover > .ico-external {
  background-image: url(../images/common/ico-external-white.png);
}
