/*
  Table
*/

@use "variables" as *;

caption {
  position: absolute;
  overflow: hidden;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  border: 0;
  clip: rect(0, 0, 0, 0);
}

table {
  width: 100%;
  table-layout: fixed;
  border-collapse: collapse;
}

.table-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;

  .title {
    position: relative;
    font-size: 18px;
    line-height: 24px;
    font-weight: bold;
    padding-left: 9px;

    &::before {
      content: '';
      position: absolute;
      top: 10px;
      left: 0;
      width: 4px;
      height: 4px;
      border-radius: 100%;
      background-color: $gray-333;
    }
  }

  .desc {
    width: 100%;
    margin-bottom: 30px;
    font-size: 16px;
    line-height: 20px;
    margin-top: 20px;
  }

  .tip {
    font-size: 15px;
    letter-spacing: -0.35px;
    text-align: right;
    color: $gray-666;
  }

  .btn-wrap {
    margin-left: auto;
  }
}

.table-caption {
  font-size: 16px;
  line-height: 20px;
  margin-bottom: 10px;
  font-weight: bold;
  padding-left: 10px;

  &::before {
    content: '- ';
  }
}

.table-foot {
  display: flex;
  justify-content: flex-end;
  margin-top: 10px;
  gap: 10px;
}

.table {
  position: relative;
  width: 100%;
  border-top: 1px solid #6f6f6f;
  color: $gray-333;

  .btn-table-desc {
    margin-left: 5px;
  }

  tr.highlight {
    td, th {
      background-color: #fffcf4;
    }
  }

  tr.with-btn-row {
    td {
      .button-group {
        flex-direction: column;
        gap: 2px;
      }
    }
  }

  td, th {
    text-align: center;
    vertical-align: middle;
    font-size: 15px;
    line-height: 24px;
    padding: 10px 20px;
    letter-spacing: -.4px;
  }

  th {
    font-weight: bold;
    border-bottom: 1px solid $gray-ddd;

    &.col-title {
      background-color: #f6f6f6;
    }
  }

  td {
    border-bottom: 1px solid $gray-ddd;

    .btn-file {
      display: flex;
      align-items: center;
      height: 28px;
    }
  }

  td.col-blue, th.col-blue {
    background-color: #f7faff;
    font-weight: 400;
  }

  .txt-lg {
    font-size: 16px !important;
  }

  a {
    &:hover {
      text-decoration: underline;
    }
  }

  .ellipse {
    white-space: nowrap;
  }

  .ellipse-target {
    max-width: 100%;
    display: inline-block;
    vertical-align: middle;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

    + .ico {
      vertical-align: middle;
      margin-left: 4px;
    }
  }

  tr.row-input {
    .col-title, th, td {
      padding: 10px 20px !important;
    }
  }

  &.v-table {
    border-top: 1px solid #6f6f6f;

    colgroup {
      col {
        &:nth-child(2n-1) {
          width: 180px;
        }
      }
    }

    .col-title {
      border-left: 1px solid $gray-ddd;
      border-right: 1px solid $gray-ddd;
    }

    th, td {
      border-top: 1px solid $gray-eee;
      border-right: 0;
      font-size: 15px;

      &:first-child {
        &.col-title {
          border-left: 0;
        }
      }

      &:last-child {
        border-right: 0;
      }
    }

    th {
      background-color: #f6f6f6;
      color: $gray-666;
      font-weight: 400;
    }

    tr {
      &:first-child {
        th, td {
          border-top: 0;
        }
      }
    }

    &.v-table-input {
      border-top-color: $gray-333;

      .form-control, .form-select {
        width: 100%;
      }

      th, td {
        padding: 9.5px 20px;

        &:first-child {
          border-left: 0;
        }

        &:last-child {
          border-right: 0;
        }

        .txt:only-child {
          line-height: 40px;
        }
      }

      .form-file {
        margin-bottom: 20px;
      }

      .file-list {
        margin-bottom: 20px;

        &:only-child {
          margin-bottom: 0;
        }
      }
    }
  }

  + .table-head {
    margin-top: 40px;
  }

  .caution {
    font-size: 15px;
    line-height: 18px;
    letter-spacing: -0.75px;
    text-align: left;
    color: #dd175d;
    padding-left: 22px;
    background-size: 16px 17px;
    background-position: left top;
    background-repeat: no-repeat;
    background-image: url(../images/common/ico-caution.svg);
    margin-top: 5px;
  }

  .btn-tip {
    width: 16px;
    height: 16px;
    display: inline-block;
    vertical-align: text-top;
  }

  .program {
    display: flex;
    flex-wrap: wrap;
    gap: 5px 4px;
    align-items: center;

    .txt {
      color: #000;
      text-decoration: underline;
      white-space: nowrap;
      max-width: 100%;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }

  .button-group {
    gap: 5px;
  }

  &.table-border-gray {
    td, th {
      border-right: 1px solid $gray-ddd;

      &:last-child {
        border-right: 0;
      }
    }
  }

  /* table variation */
  /*   */
  &.table-notice {
    td, th {
      font-size: 15px;
    }

    //tr.highlight {
    //  td, th {
    //    padding: 14.75px 20px;
    //  }
    //}

    col {
      &:nth-child(1) {
        width: 8%
      }

      &:nth-child(2) {
        width: 56%
      }

      &:nth-child(3) {
        width: 8%
      }

      &:nth-child(4) {
        width: 10%
      }

      &:nth-child(5) {
        width: 8%
      }
    }

    .ellipse-target {
      max-width: calc(100% - 47px);
    }
  }

  /*   */
  &.table-worksheet {
    .btn-file {
      justify-content: center;
    }

    col {
      &:nth-child(1) {
        width: 8%
      }

      &:nth-child(2) {
        width: 60%
      }

      &:nth-child(3) {
        width: 9%
      }

      &:nth-child(4) {
        width: 15%
      }

      &:nth-child(5) {
        width: 8%
      }
    }
  }

  /*    */
  &.table-related-laws {
    .btn-file {
      justify-content: center;
    }

    col {
      &:nth-child(1) {
        width: 10%
      }

      &:nth-child(2) {
        width: 60%
      }

      &:nth-child(3) {
        width: 15%
      }

      &:nth-child(4) {
        width: 15%
      }
    }
  }

  /* 1:1   */
  &.table-inquiry {
    col {
      &:nth-child(1) {
        width: 8%
      }

      &:nth-child(2) {
        width: 15%
      }

      &:nth-child(3) {
        width: 40%
      }

      &:nth-child(4) {
        width: 12%
      }

      &:nth-child(5) {
        width: 10%
      }

      &:nth-child(6) {
        width: 15%
      }
    }
  }

  .empty {
    padding: 20px 0 !important;
    color: $gray-666;
    text-align: center;
    font-size: 15px;

    > span {
      display: block;
      padding: 130px 0;
      background-color: #f6f6f6;
      text-align: center;
    }
  }

  &.table-hover {
    tbody {
      tr:hover {
        td {
          background-color: #f4f7fc !important;
        }
      }
    }
  }

  //  >   -  
  &.table-judge-info {
    col {
      &:nth-child(1) {
        width: 4.7%
      }

      &:nth-child(2) {
        width: 21%
      }

      &:nth-child(3) {
        width: 13%
      }

      &:nth-child(4) {
        width: 7.7%
      }

      &:nth-child(5) {
        width: 14%
      }

      &:nth-child(6) {
        width: 12.3%
      }

      &:nth-child(7) {
        width: 10.3%
      }

      &:nth-child(8) {
        width: 17%
      }
    }
  }

  .mo-empty {
    display: none;
  }
}

@media (max-width: 1050px) {
  .table {
    td, th {
      padding: 12px 12px;
    }

    /* table variation */
    &.v-table {
      colgroup {
        col {
          &:nth-child(2n-1) {
            width: 120px;
          }
        }
      }

      td, th {
        padding: 10px;
      }

      tr.row-input {
        .col-title, th, td {
          padding: 10px;
        }
      }

      &.v-table-input {
        border-top: 0;
        border-bottom: 0;

        table {
          display: block;

          thead, tbody, tr, td, th {
            display: block;
            padding: 0;
            border: 0;

            > span {
              line-height: initial;
            }
          }

          td {
            width: 100%;
          }

          tr + tr {
            margin-top: 20px;
          }

          .col-title {
            background-color: transparent;
            text-align: left;
            margin-bottom: 2px;
            padding: 0;
          }
        }
      }
    }

    /*   */
    &.table-notice {
      col {
        &:nth-child(1) {
          width: 15%
        }

        &:nth-child(2) {
          width: 60%
        }

        &:nth-child(3) {
          width: 25%
        }

        &:nth-child(4) {
          display: none;
        }

        &:nth-child(5) {
          display: none;
        }
      }

      th, td {
        &:nth-child(4) {
          display: none;
        }

        &:nth-child(5) {
          display: none;
        }
      }
    }

    /*   */
    &.table-worksheet {
      col {
        &:nth-child(1) {
          width: 50px;
          white-space: nowrap;
        }

        &:nth-child(2) {
          width: calc(100% - 50px - 80px - 105px);
        }

        &:nth-child(3) {
          width: 80px;
        }

        &:nth-child(4) {
          width: 105px;
          white-space: nowrap;
        }

        &:nth-child(5) {
          display: none;
        }
      }

      td, th {
        &:nth-child(5) {
          display: none;
        }
      }
    }

    /*    */
    &.table-related-laws {
      col {
        &:nth-child(1) {
          width: 50px;
          white-space: nowrap;
        }

        &:nth-child(2) {
          width: calc(100% - 50px - 120px);
        }

        &:nth-child(3) {
          width: 120px;
          white-space: nowrap;
        }
      }
    }

    /* 1:1   */
    &.table-inquiry {
      col {
        &:nth-child(1) {
          width: 15%
        }

        &:nth-child(2) {
          display: none;
        }

        &:nth-child(3) {
          width: 55%
        }

        &:nth-child(4) {
          width: 30%
        }

        &:nth-child(5) {
          display: none;
        }

        &:nth-child(6) {
          display: none
        }
      }

      th, td {
        &:nth-child(2) {
          display: none;
        }

        &:nth-child(5) {
          display: none;
        }

        &:nth-child(6) {
          display: none;
        }
      }
    }
  }
}

@media (max-width: 768px) {
  .table {
    &.v-table {
      colgroup {
        display: none;
      }

      tr {
        display: flex;
        flex-wrap: wrap;
      }

      td, th {
        padding: 17.5px 10px;

        &:nth-child(2n-1) {
          width: 120px;
          flex-shrink: 0;
          border-left: 0;
        }

        &:nth-child(2n) {
          width: calc(100% - 120px);
        }
      }
    }
  }
}

.table-container {
  overflow-x: auto;

  .table {
    min-width: 1200px;
  }
}

.mo-table {
  display: none !important;
}

.table-fixed {
  position: relative;

  .table-fixed-head {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1;
    padding-right: 5px;

    th, td {
      height: 45px;
    }
  }

  .table-fixed-body {
    width: 100%;
    max-height: 570px;
    overflow-x: hidden;
    overflow-y: auto;
    margin-top: 45px;
  }
}

@media (max-width: $breakpoint-xlarge) {
  .table {
    //  >   -  
    &.table-judge-info {
      col {
        &:nth-child(1) {
          display: none;
        }

        &:nth-child(2) {
          width: 35%
        }

        &:nth-child(3) {
          display: none;
        }

        &:nth-child(4) {
          display: none;
        }

        &:nth-child(5) {
          width: 25%
        }

        &:nth-child(6) {
          width: 20%
        }

        &:nth-child(7) {
          width: 20%
        }

        &:nth-child(8) {
          display: none;
        }
      }

      tr:not(.mo-empty) {
        th, td {
          &:nth-child(1) {
            display: none;
          }

          &:nth-child(3) {
            display: none;
          }

          &:nth-child(4) {
            display: none;
          }

          &:nth-child(8) {
            display: none;
          }
        }
      }
    }

    .mo-empty {
      display: table-row;
    }

    .pc-empty {
      display: none;
    }
  }

  .pc-table {
    display: none !important;
  }

  .mo-table {
    display: table !important;
  }

  .table-fixed {
    .table-container {
      overflow-x: visible;
    }

    .table-fixed-head, .table-fixed-body {
      min-width: 1205px;
    }

    .table-fixed-body {
      overflow-x: visible;
    }
  }
}

@media (max-width: $breakpoint-medium) {
  .table-head {
    .title {
      font-size: 17px;
    }
  }
}