/*
  Button
*/
@use "variables" as *;

$button-height: 34px;
$button-min-width-lg: 120px;
$button-min-width-sm: 60px;
$button-line-height: 32px;
$button-height-lg: 54px;
$button-line-height-lg: 52px;
$button-height-sm: 28px;
$button-line-height-sm: 26px;
$button-font-size: 17px;
$button-font-size-lg: 18px;
$button-font-size-sm: 14px;
$button-font-color: $white;
$button-font-weight: 400;
$button-letter-spacing: -0.8px;
$button-border-color: $point;
$button-bg: $point;
$button-radius: 5px;
$button-padding: 0 15px;
$button-padding-sm: 0 12px;
$button-padding-ico: 0 15px;
$button-padding-lg: 0 30px;
$button-color-sub: #E97EBDFF;
$button-color-gray: $gray-666;
$button-color-black: #454545;
$button-bg-red: $red;
$button-color-white: $white;
$button-disabled-border: $gray-ccc;
$button-disabled-bg: $gray-eee;
$button-disabled-color: $button-color-gray;

a, button {
  text-decoration: none;
  border: 0;
  cursor: pointer;
}

.btn {
  position: relative;
  display: inline-block;
  min-width: 80px;
  text-align: center;
  vertical-align: middle;
  height: $button-height;
  line-height: $button-line-height;
  font-size: $button-font-size;
  font-weight: $button-font-weight;
  letter-spacing: $button-letter-spacing;
  color: $button-font-color;
  overflow: hidden;
  border-radius: $button-radius;
  border: 1px solid $button-border-color;
  background-color: $button-bg;
  padding: $button-padding;
  transition: background-color .3s;
  white-space: nowrap;
  text-decoration: none !important;
  cursor: pointer;

  &:hover {
    background-color: #085cb1;
  }

  &:disabled {
    cursor: default !important;
    color: $button-disabled-color !important;
    background-color: $button-disabled-bg !important;
    border: 1px solid $button-disabled-border !important;
  }

  &.with-icon {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    gap: 5px;
    padding: $button-padding-ico;

    //span {
    //  padding-top: 2px;
    //}

    .ico {
      flex-shrink: 0;
    }
  }

  &.btn-lg {
    min-width: $button-min-width-lg;
    padding: $button-padding-lg;
    height: $button-height-lg;
    line-height: $button-line-height-lg;
    font-size: $button-font-size-lg;
  }

  &.btn-sm {
    min-width: $button-min-width-sm;
    height: $button-height-sm;
    line-height: $button-line-height-sm;
    font-size: $button-font-size-sm;
    padding: $button-padding-sm;
    border-radius: 3px;
  }

  &.btn-black {
    background-color: $button-color-black;
    border-color: $button-color-black;

    &:hover {
      background-color: #2f2f2f;
    }
  }

  &.btn-red {
    background-color: $button-bg-red;
    border-color: $button-bg-red;

    &:hover {
      background-color: $button-bg-red;
    }
  }

  &.btn-border-point {
    background-color: $button-color-white;
    color: $dark-blue;

    &:hover {
      background-color: #e8f2ff;
    }
  }

  &.btn-light-blue {
    background-color: #eff1f7;
    color: $dark-blue;
    border-color: #eff1f7;
  }

  &.btn-border-gray {
    border-color: $gray-999;
    background-color: $button-color-white;
    color: $gray-222;

    &:hover {
      border-color: #003675;
      background-color: #003675;
      color: $button-color-white;
    }
  }

  &.btn-block {
    display: block;
    width: 100%;
  }
}

.button-group {
  display: flex;
  gap: 10px;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;

  &.button-column-group {
    flex-direction: column;
  }
}

.btn-file {
  display: flex;
  align-items: center;

  .ico {
    margin-right: 5px;
  }
}

.btn-like {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 28px;
  height: 28px;
  background-color: rgba(0, 0, 0, .3);
  border-radius: 100%;
}

.btn-favorite {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: center;
  gap: 10px;
}

.btn-check {
  display: inline-flex;
  color: $white;

  .form-check {
    padding: 0 10px 0 35px;
    height: 28px;
    border-radius: $button-radius;
    background-color: $point;

    input {
      + span {
        font-size: 15px;
        font-weight: 500;
        line-height: 28px;
        letter-spacing: -0.35px;

        &::before {
          top: 4px;
          left: 10px;
          background-color: $white;
          border-color: $dark-blue;
        }

        &::after {
          top: 9px;
          left: 12px;
        }
      }
    }
  }
}