/*
  Badge
*/

@use "variables" as *;

.badge-wrap {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 5px;
}

.badge {
  display: inline-flex;
  justify-content: center;
  gap: 3px;
  align-items: center;
  height: 21px;
  line-height: 19px;
  border-radius: 2px;
  font-size: 13px;
  font-weight: 500;
  text-align: center;
  padding: 0 7px;
  letter-spacing: -.65px;
  border: 1px solid #009bd5;
  background-color: #009bd5;
  color: $white;

  &.badge-blue {
    border-color: #009bd5;
    background-color: #009bd5;
    color: #fff;
  }

  &.badge-green {
    border-color: #56b454;
    background-color: #56b454;
    color: $white;
  }

  &.badge-purple {
    border-color: #ccc7e9;
    background-color: #edeaff;
    color: #4a3a9d;
  }

  &.badge-pink {
    border-color: #edc8d9;
    background-color: #ffeef6;
    color: #d11e6e;
  }

  &.badge-skyblue {
    border-color: #badee7;
    background-color: #dcf5fb;
    color: #0b7b9c;
  }

  &.badge-red {
    border-color: #f5cad7;
    background-color: #ffecf2;
    color: #9c2348;
  }

  &.badge-hotpink {
    border-color: #df4e69;
    background-color: #df4e69;
    color: #fff;
  }

  &.badge-sm {
    height: 17px;
    line-height: 15px;
    padding: 0 5px;
  }

  &.badge-circle {
    border-radius: 10.5px;
  }
}