/*
  Pagination
*/

@use "variables" as *;

$pagination-gap: 8px;
$pagination-min-width: 30px;
$pagination-height: 30px;
$pagination-font-size: 15px;
$pagination-letter-spacing: -0.75px;
$pagination-color: $gray-666;
$pagination-active-color: #454545;
$pagination-hover-color: $gray-ccc;
$pagination-border-color: transparent;
$pagination-hover-border-color: $gray-ccc;
$pagination-padding: 0 11px;
$pagination-radius: 5px;
$pagination-line-height: 30px;

.pagination {
  display: flex;
  justify-content: center;
  gap: 8px;

  .page-item {
    .page-link {
      display: flex;
      justify-content: center;
      align-items: center;
      min-width: $pagination-min-width;
      height: $pagination-height;
      text-align: center;
      font-size: $pagination-font-size;
      line-height: $pagination-line-height;
      letter-spacing: $pagination-letter-spacing;
      color: $pagination-color;
      padding: $pagination-padding;
      border: 1px solid $pagination-border-color;
      border-radius: $pagination-radius;
      transition: background-color .2s, border .2s;

      &:hover {
        border-color: $pagination-hover-border-color;
      }

      &:focus-visible {
        outline-offset: 2px;
      }
    }

    &.active {
      .page-link {
        font-weight: bold;
        color: $white;
        background-color: $pagination-active-color;
        border-color: $pagination-active-color;
      }
    }

    &.disabled {
      opacity: .7;

      .page-link {
        cursor: default;
        border-color: transparent;
      }

      .ico {
        opacity: .5;
      }
    }

    &.prev, &.first, &.last, &.next {
      .page-link {
        width: 30px;
        padding: 0;
      }

      &.disabled {
        .page-link {
          &:hover {
            background-color: transparent;
          }
        }
      }
    }
  }
}