/*
   
*/
@use "variables" as *;
@use "config" as *;

.sub-contents {
  .sub-inner {
    max-width: $contents-size;
    margin: 0 auto;
  }

  .sub-heading {
    width: 1200px;
    margin: 30px auto 36px;
    height: 140px;
    background-repeat: no-repeat;
    //background-image: url(../images/sub/bg-heading.png);
    background-size: cover;
    background-position: center;
    color: $white;
    border-radius: 10px;

    .title {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;
      font-size: 24px;
      font-weight: bold;
    }
  }

  .mo-nav-active {
    display: none;
    width: 100%;
    color: $white;

    .btn-sub-dropdown {
      position: relative;
      display: block;
      text-align: center;
      width: 100%;
      height: 50px;
      font-size: 20px;
      font-weight: 500;
      line-height: 50px;
      letter-spacing: -1px;
      background-color: #0e75dc;
      border-radius: 0 0 10px 10px;

      &.open {
        border-radius: 0;

        &::after {
          transform: translateY(-50%) rotate(180deg);
        }

        .sub-title {
          display: block;
        }
      }

      &::after {
        content: '';
        position: absolute;
        width: 13px;
        height: 8px;
        right: 19px;
        top: 50%;
        transform: translateY(-50%);
        background-image: url(../images/common/ico-sub-dropdown.png);
        background-size: cover;
        background-repeat: no-repeat;
        transition: .2s;
      }
    }

    .sub-title {
      display: none;
      text-align: center;
      width: 100%;
      height: 46px;
      font-size: 18px;
      font-weight: 500;
      line-height: 46px;
      letter-spacing: -.9px;
      background-color: rgba(44, 52, 72, .9);
    }
  }

  .sub-nav {
    position: relative;
    background: linear-gradient(to right, transparent calc(50% + ($contents-size / 2)), rgba(1, 29, 63, 0.85) calc(50% + ($contents-size / 2)));

    .nav {
      display: flex;
      height: 50px;
      color: $gray-ccc;

      .nav-item {
        position: relative;
        //width: 240px;
        flex: 1;
        height: 100%;

        &:first-child {
          .nav-title {
            border-radius: 0 0 0 10px;
          }

          &::before {
            content: none;
          }
        }

        &::before {
          content: '';
          position: absolute;
          left: 0;
          top: 0;
          width: 1px;
          height: 100%;
          background-color: rgba(255, 255, 255, .2);
          z-index: 1;
        }

        &:last-child {
          .nav-title {
            border-radius: 0 0 10px 0;
          }
        }

        .nav-title {
          position: relative;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 18px;
          line-height: 50px;
          letter-spacing: -0.9px;
          overflow: hidden;
          background-color: rgba(1, 29, 63, .85);
          transition: font-size .1s;

          > a {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            height: 100%;
            padding: 0 10px;

            &:focus-visible {
              outline: 2px solid $white;
              outline-offset: 2px;
            }
          }

          .btn-toggle {
            position: absolute;
            display: flex;
            align-items: center;
            justify-content: center;
            right: 10px;
            top: 50%;
            transform: translateY(-50%);
            width: 21px;
            height: 20px;
            border-radius: 3px;
            //border: solid 1px rgba(255, 255, 255, 0.2);
            background-color: rgba(255, 255, 255, 0.2);

            .ico {
              transition: .3s;
            }
          }
        }

        &.on {
          .nav-title {
            background-color: $point;
            color: $white;
            font-weight: 500;
            font-size: 20px;

            .btn-toggle {
              border: solid 1px rgba(255, 255, 255, 0.2);
            }
          }
        }

        &.show {
          .btn-toggle {
            .ico {
              transform: rotate(180deg);
            }
          }
        }
      }

      .sub-menu {
        display: none;
        position: absolute;
        top: 50px;
        left: 0;
        width: 100%;
        z-index: 3;
        background-color: rgba(65, 72, 90, .95);
        border: 1px solid #252f4a;
        border-top: 1px dashed #4a5e79;
        border-radius: 0 0 3px 3px;

        li {
          position: relative;

          a {
            display: block;
            width: 100%;
            height: 47px;
            line-height: 47px;
            font-size: 18px;
            letter-spacing: -0.9px;
            white-space: nowrap;
            padding: 0 10px;
            text-align: center;
            transition: .2s;
            color: $white;
          }

          + li {
            &::after {
              content: '';
              position: absolute;
              top: 0;
              left: 0;
              width: 100%;
              height: 1px;
              border-top: 1px dashed rgba(255, 255, 255, .2);
              //background-color: rgba(255, 255, 255, .2);
            }
          }
        }
      }
    }
  }

  .section-title {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;

    .title {
      display: flex;
      flex-wrap: wrap;
      word-break: break-word;
      font-size: 24px;
      font-weight: bold;
      line-height: 24px;
      letter-spacing: -1.2px;
      gap: 5px;
      align-items: center;
      color: $gray-000;
      flex-shrink: 0;
    }

    .ico-title {
      position: relative;
      display: inline-block;
      width: 16px;
      height: 16px;
      border: solid 2px $gray-666;
      border-radius: 100%;

      &::after {
        content: '';
        position: absolute;
        width: 8px;
        height: 8px;
        border: solid 2px $gray-666;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        border-radius: 100%;
      }
    }

    .breadcrumb {
      margin-left: auto;

      &:only-child {
        margin-bottom: 10px;
      }
    }

    .contents-info {
      padding: 0;
      background-color: transparent;
      border-color: transparent;

      .item {
        .ico-tip {
          top: 2px;
        }
      }
    }
  }

  .section-desc {
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -.4px;
    color: $gray-222;
  }

  .page-title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 28px;

    .title {
      position: relative;
      display: flex;
      flex-wrap: wrap;
      word-break: break-word;
      gap: 5px;
      align-items: center;
      color: #000;
      flex-shrink: 0;
      font-size: 30px;
      font-weight: bold;
      line-height: 32px;
      letter-spacing: -1.5px;
    }
  }
}

@media (max-width: 1200px) {
  .sub-contents {
    .sub-heading {
      width: calc(100% - 30px);
      margin: 20px auto;

      .title {
        padding: 35px 0;
      }
    }

    .sub-inner {
      padding: 0 15px;
    }

    .sub-nav {
      display: none;
      position: relative;
      width: 100%;
      height: auto;
      z-index: 5;
      top: auto;
      left: auto;
      background-color: transparent;
      border: 0;

      .nav {
        height: auto;
        flex-direction: column;

        .nav-item {
          width: auto;
          flex: 1;

          &:first-child {
            .nav-title {
              border-radius: 0;
            }
          }

          &:last-child {
            .nav-title {
              border-radius: 0 0 10px 10px;
            }
          }

          .nav-title {
            background-color: #0065c9;
            font-size: 20px;
          }

          > a {
            transition: background-color .2s;
            color: #ccc;
          }

          &.on {
            //display: none;

            .nav-title {
              background-color: #0065c9;
            }
          }

          &.show {
            .nav-title {
              &:not(:only-child) {
                border-bottom: 1px dashed #4a5e79;
              }
            }
          }

          + .nav-item {
            &::after {
              content: '';
              position: absolute;
              top: 0;
              left: 0;
              width: 100%;
              height: 1px;
              background-color: rgba(255, 255, 255, .2);
            }
          }

          .sub-menu {
            li {
              a {
                background-color: rgba(44, 52, 72, .9);
              }
            }
          }
        }
      }

      &.mo-show {
        display: block;
      }
    }

    .mo-nav-active {
      display: block;
    }
  }
}

@media (max-width: $breakpoint-xlarge) {
  .sub-contents {
    padding-bottom: 0;

    .page-title {
      margin-bottom: 26px;

      .title {
        display: none;
      }
    }
  }
}

@media (max-width: $breakpoint-medium) {
  .sub-contents {
    .section-title {
      flex-direction: column-reverse;
      justify-content: space-between;
      gap: 10px;

      .title {
        order: 1;
      }

      .breadcrumb {
        margin: 0;
      }

      .contents-info {
        order: 0;
      }
    }
  }
}

.board {
  .board-head {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 15px;

    .total {
      font-size: 15px;
      line-height: 18px;
      letter-spacing: -0.75px;
      color: $gray-666;

      .num {
        color: $gray-000;
        font-weight: 500;
      }
    }

    .sorting {
      display: flex;
      gap: 10px;
      margin-left: auto;

      .form-group {
        display: flex;
        align-items: center;
        gap: 5px;
      }

      .form-select {
        width: 110px;
        font-weight: 500;
      }

      .form-search {
        display: flex;
        gap: 5px;

        .form-input {
          width: 280px;
        }

        .btn {
          flex-shrink: 0;
        }
      }

      .button-group {
        gap: 10px;
      }

      .ico-search-blue {
        flex-shrink: 0;
      }
    }
  }

  .board-foot {
    margin-top: 30px;
  }

  &.board-notice {
    .table {
      td, th {
        border-right: 0;

        &:first-child {
          border-left: 0;
        }
      }

      td {
        &:nth-child(2) {
          .ico-volume {
            display: none;
          }
        }
      }

      tr {
        a:hover {
          text-decoration: underline;
        }

        &.bg-yellow {
          background-color: #fffbe5;

          td {
            background-color: #fffbe5;
          }
        }
      }
    }
  }

  &.board-qna {
    .table {
      .ellipse {
        .ellipse-target {
          max-width: 100%;
        }
      }

      td, th {
        border-right: 0;

        &:first-child {
          border-left: 0;
        }
      }

      a {
        &:hover {
          text-decoration: underline;
        }
      }
    }
  }

  &.board-debate {
    .table {
      .ellipse {
        .ellipse-target {
          max-width: 100%;
        }
      }

      td, th {
        border-right: 0;

        &:first-child {
          border-left: 0;
        }
      }
    }
  }
}

@media (max-width: $breakpoint-xlarge) {
  .board {
    &.board-notice {
      .board-head {
        .form-group {
          display: none;
        }
      }

      .table {
        table {
          display: block;
        }

        thead {
          display: none;
        }

        tbody, tr {
          display: block;
        }

        th, td {
          padding: 0;
          border-top: 0;
          border-bottom: 0;

          > span {
            line-height: initial;
            font-size: 13px;
            transform: translateY(-2px);
            color: $gray-666;
          }

          .ico-file {
            width: 10px;
            height: 15px;
          }

          &::after {
            content: '';
            display: inline-block;
            width: 1px;
            height: 10px;
            background-color: $gray-ccc;
            margin: 0 10px;
          }

          a {
            text-decoration: underline;
            font-weight: 500;
          }

          &:first-child {
            display: none;
          }

          &:nth-child(2) {
            display: block;
            margin-bottom: 8px;

            .ellipse-target {
              max-width: calc(100% - 48px);
              color: $black;
              font-weight: 500;
            }

            .ico-volume {
              display: inline-block;
              vertical-align: middle;
              transform: translateY(-1px);
            }

            &::after {
              content: none;
            }
          }

          &:nth-child(3) {
            a {
              text-decoration: none;

              .ico-file {
                width: 16px;
              }
            }
          }

          &:nth-child(4) {
            //&::before {
            //  content: '';
            //  display: inline-block;
            //  font-size: 13px;
            //  line-height: 15px;
            //  color: $gray-666;
            //  margin-right: 5px;
            //}
          }

          &:nth-child(5) {
            //&::before {
            //  display: inline-block;
            //  font-size: 13px;
            //  line-height: 15px;
            //  color: $gray-666;
            //  margin-right: 5px;
            //}

            &::after {
              content: none;
            }
          }
        }

        td {
          &:empty {
            display: none;
          }
        }

        tr {
          padding: 20px 5px;

          + tr {
            border-top: 1px solid $gray-eee;
          }

          &:last-child {
            border-bottom: 1px solid $gray-eee;
          }
        }
      }
    }

    &.board-qna {
      .board-head {
        .form-group {
          .form-select {
            display: none;
          }
        }
      }

      .table {
        table {
          display: block;
        }

        thead {
          display: none;
        }

        tbody, tr {
          display: block;
        }

        th, td {
          padding: 0;
          border-top: 0;
          border-bottom: 0;

          > span {
            line-height: initial;
            font-size: 13px;
            transform: translateY(-2px);
            color: $gray-666;
          }

          a {
            text-decoration: underline;
            font-weight: 500;
          }

          &:first-child {
            display: none;
          }

          &:nth-child(2) {
            display: none;
          }

          &:nth-child(3) {
            display: block;
            margin-bottom: 8px;
          }

          &:nth-child(5), &:nth-child(6) {
            display: none;
          }
        }

        tr {
          padding: 20px 5px;

          &:last-child {
            border-bottom: 1px solid $gray-eee;
          }

          + tr {
            border-top: 1px solid $gray-eee;
          }
        }
      }
    }

    &.board-debate {
      .table {
        table {
          display: block;
        }

        thead {
          display: none;
        }

        tbody, tr {
          display: block;
        }

        th, td {
          padding: 0;
          border-top: 0;
          border-bottom: 0;

          > span {
            line-height: initial;
            font-size: 13px;
            transform: translateY(-2px);
            color: $gray-666;
          }

          a {
            text-decoration: underline;
            font-weight: 500;
          }

          &:first-child {
            display: none;
          }

          &:nth-child(3) {
            display: block;
            margin-bottom: 15px;
          }

          &:nth-child(5) {
            &::before {
              content: '';
              display: inline-block;
              width: 1px;
              height: 10px;
              background-color: $gray-ccc;
              margin: 0 10px;
              transform: translateY(-1px);
            }
          }

          &:nth-child(6) {
            display: none;
          }
        }

        tr {
          padding: 20px 5px;

          &:last-child {
            border-bottom: 1px solid $gray-eee;
          }

          + tr {
            border-top: 1px solid $gray-eee;
          }
        }
      }
    }
  }
}

@media (max-width: 700px) {
  .board {
    .board-head {
      position: relative;
      width: 100%;

      .total {
        display: none;
        position: absolute;
        left: 0;
        bottom: 10px;
      }

      .sorting {
        width: 100%;
        flex-direction: column;
        align-items: flex-end;
        gap: 10px 5px;

        .form-group {
          flex-wrap: wrap;
          width: 100%;
        }

        .form-select {
          width: 100px;

          &:nth-child(3) {
            flex: 1;
          }
        }

        .form-search {
          width: calc(100% - 100px - 5px);

          .form-input {
            width: calc(100% - 5px - 90px);
          }
        }

        .btn {
          width: 90px;
        }

        &.sorting-lg {
          flex-wrap: wrap;
          flex-direction: row;

          .form-select {
            width: calc(50% - 2.5px);
          }

          .form-search {
            width: 100%;
          }
        }
      }
    }
  }
}

.cs-service-info {
  max-width: $contents-size;
  display: flex;
  align-items: center;
  width: 100%;
  border-radius: 5px;
  background-color: #eff2f7;
  padding: 26px 0;
  margin: 120px auto 0;

  .col-call {
    display: flex;
    width: 536px;
    padding-left: 136px;

    .info {
      width: calc(100% - 58px - 10px);
      margin-left: 10px;
    }

    .detail {
      display: flex;
      align-items: flex-end;
      margin-bottom: 5px;

      .title {
        font-size: 20px;
        font-weight: bold;
        line-height: 28px;
        letter-spacing: -1px;
        color: #424c67;
        margin-right: 20px;
      }

      .tel {
        font-size: 36px;
        font-weight: bold;
        line-height: 38px;
        letter-spacing: -0.9px;
        text-align: left;
        color: #1778fb;
      }
    }

    .time {
      font-size: 15px;
      font-weight: 500;
      line-height: 20px;
      letter-spacing: -0.7px;
      color: #515a72;
    }
  }

  .col-detail {
    position: relative;
    width: calc(100% - 536px);
    padding-left: 50px;

    &::before {
      content: '';
      position: absolute;
      width: 1px;
      height: 56px;
      background-image: url(../images/common/line-black.png);
      background-size: 1px 56px;
      background-repeat: no-repeat;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }

    .detail {
      font-size: 16px;
      line-height: 30px;
      letter-spacing: -0.8px;
      color: #515a72;
    }
  }
}

@media (max-width: $breakpoint-xlarge) {
  .cs-service-info {
    position: relative;
    max-width: none;
    padding: 20px 20px 40px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    border-radius: 0;

    * {
      word-break: keep-all;
    }

    .ico-cs {
      position: absolute;
      top: -25px;
      left: 50%;
      transform: translateX(-50%);
      width: 50px;
      height: 50px;
    }


    .col-call {
      width: 100%;
      padding: 0;

      .info {
        width: 100%;
        margin-left: 0;
      }

      .detail {
        flex-wrap: wrap;
        justify-content: center;
        margin-top: 18px;
        gap: 5px 20px;
        text-align: center;

        .title {
          margin: 0;
        }

        .tel {
          font-size: 30px;
          line-height: 32px;
        }
      }
    }

    .col-detail {
      width: 100%;
      padding: 0;
      margin-top: 15px;

      .detail {
        font-size: 15px;
        line-height: 20px;

        br {
          display: none;
        }

        .con {
          display: block;

          &:not(&:last-child) {
            margin-bottom: 8px;
          }
        }
      }

      &::before {
        content: none;
      }
    }
  }
}

.board-view {
  border-top: 1px solid #6f6f6f;

  .board-head {
    padding-top: 0;

    .title {
      font-size: 22px;
      font-weight: bold;
      line-height: 24px;
      color: #222;
      padding: 23px 30px;
      background-color: #f6f6f6;
    }

    .view-info {
      padding: 20px 30px;
      border-bottom: 1px solid $gray-ddd;

      .info {
        display: flex;
        justify-content: space-between;
        margin-bottom: 20px;

        .item, .item-mo {
          position: relative;
          display: flex;
          align-items: center;
          font-size: 16px;
          line-height: 16px;
          letter-spacing: -.4px;
          color: $gray-666;
          gap: 10px;

          .val {
            color: #222;
          }

          + .item, + .item-mo {
            &::before {
              content: '';
              position: absolute;
              width: 1px;
              height: 10px;
              top: 2px;
              left: -22px;
              background-color: $gray-ccc;
            }
          }
        }

        .item-mo {
          display: none;
        }

        .left, .right {
          display: flex;
          gap: 10px 40px;
        }
      }

      .file-list {
        width: 100%;

        .file {
          display: flex;
          width: 100%;
          gap: 10px;

          .ico {
            flex-shrink: 0;
          }

          a {
            font-size: 16px;
            font-weight: 500;
            line-height: 16px;
            color: $dark-blue;

            &:hover {
              text-decoration: underline;
            }
          }

          + .file {
            margin-top: 10px;
          }
        }
      }
    }
  }

  .board-body {
    padding: 30px 30px 40px;
    border-bottom: 1px solid $gray-ddd;
    //min-height: 430px;

    .view-content {
      font-size: 16px;
      line-height: 30px;
      letter-spacing: -.4px;
      color: #222;
    }
  }

  .board-nav {
    .nav {
      display: flex;
      width: 100%;
      align-items: center;
      padding: 0 30px;
      gap: 68px;
      background-color: #f6f6f6;
      height: 70px;
      border-bottom: 1px solid $gray-ddd;

      .tit {
        position: relative;
        width: 50px;
        font-size: 16px;
        line-height: 16px;
        color: $gray-666;

        &::after {
          content: '';
          position: absolute;
          width: 16px;
          height: 9px;
          right: -34px;
          top: 50%;
          transform: translateY(-50%);
          background-image: url(../images/community/ico-arrow-down.png);
          background-size: cover;
        }
      }

      &.nav-prev {
        .tit {
          &::after {
            background-image: url(../images/community/ico-arrow-down.png);
            transform: translateY(-50%) rotate(180deg);
          }
        }
      }

      .val {
        font-size: 16px;
        @include ellipsis(1, 16px);
        width: calc(100% - 50px - 16px);
        color: $gray-666;
      }

      a:hover {
        text-decoration: underline;
      }
    }
  }

  .board-foot {
    .button-group {
      margin-top: 30px;
    }
  }

  .board-body {
    + .board-head {
      border-top: 1px solid $line;
    }
  }
}

@media (max-width: $breakpoint-medium) {
  .board-view {
    .board-head {
      .title {
        line-height: 30px;
        padding: 20px 15px;
      }

      .view-info {
        padding: 20px 15px;

        .info {
          flex-wrap: wrap;
          gap: 10px;

          .item {
            gap: 10px;

            .tit {
              display: none;
            }

            + .item {
              &::before {
                left: -9px;
              }
            }
          }

          .item-mo {
            display: flex;
          }

          .left {
            gap: 10px 20px;
          }

          .right {
            display: none;
          }
        }

        .left, .right {
          width: 100%;
          flex-wrap: wrap;
        }

        .file-list {
          .file {
            a {
              line-height: 20px;
            }
          }
        }
      }
    }

    .board-body {
      padding: 30px 15px;
    }
  }
}