/*
  Popup
*/

@use "variables" as *;
@use "config" as *;

$popup-width: 640px;
$popup-sm-width: 440px;
$popup-lg-width: 800px;
$popup-xl-width: 1000px;

.dim {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.3;
  transition: opacity .2s;
  z-index: 100;
  background-color: rgba(0,0,0,.6);
}

.popup {
  position: fixed;
  width: $popup-width;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: none;
  z-index: 105;
  background-color: $white;
  padding: 20px 30px 30px;
  border-radius: 5px;
  box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.1);

  .popup-head {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid $gray-eee;
    padding-bottom: 10px;

    .title {
      font-size: 20px;
      line-height: 24px;
      font-weight: bold;
      letter-spacing: -1px;
      color: $gray-000;
    }

    .btn-popup-close {
      display: flex;
      width: 40px;
      height: 40px;
      align-items: center;
      padding-right: 9px;
      justify-content: flex-end;
    }
  }

  .popup-body {
    overflow-y: auto;
    overflow-x: hidden;
    max-height: calc(100dvh - 300px);
    padding: 20px 0;

    .content {
      min-height: 322px;
      font-size: 15px;
      line-height: 24px;
    }

    .contents-info {
      margin-bottom: 20px;
    }

    .form-group {
      + .form-group {
        margin-top: 20px;
      }

      .number-group {
        display: flex;
        align-items: center;

        .dash {
          margin: 0 10px;
        }
      }
    }

    .table + .button-group {
      margin-top: 20px;
    }

    .table-container {
      .table {
        min-width: 600px;
      }
    }

    .button-group + .table-container {
      margin-top: 20px;
    }
  }

  .popup-footer {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    margin: 10px 0 0;
  }

  &.popup-sm {
    width: $popup-sm-width;
  }

  &.popup-lg {
    width: $popup-lg-width;
  }

  &.popup-xl {
    width: $popup-xl-width;
  }

  &.popup-terms {
    .form-group {
      margin-bottom: 20px;

      .form-select {
        width: 50%;
      }
    }

    .popup-body {
      padding-top: 20px;
    }

    .popup-content {
      line-height: normal;

      .content {
        display: none;
        padding-right: 10px;

        &.active {
          display: block;
        }

        + .form-group {
          margin-top: 100px;
        }
      }

      .title {
        display: flex;
        align-items: center;
        font-size: 18px;
        font-weight: 500;
        line-height: 20px;
        letter-spacing: -0.9px;

        img {
          display: inline-block;
          width: 35px;
          height: auto;
          margin-right: 5px;
        }

        + .bullet {
          margin-top: 13px;
        }
      }

      .desc {
        font-size: 15px;
        line-height: 24px;
        letter-spacing: -0.75px;
        margin-top: 13px;
        margin-bottom: 10px;

        + .title {
          margin-top: 20px;
        }

        &:last-child {
          margin-bottom: 0;
        }
      }

      .bullet {
        position: relative;
        font-size: 15px;
        line-height: 24px;
        letter-spacing: -0.75px;
        padding-left: 20px;
        color: $gray-666;

        &.with-btn {
          display: flex;
          align-items: center;
          line-height: 30px;

          &::before {
            top: 14px;
          }

          + .bullet {
            margin-top: 10px;
          }
        }

        + .title {
          margin-top: 20px;
        }

        &::before {
          content: '';
          position: absolute;
          top: 10px;
          left: 10px;
          width: 3px;
          height: 3px;
          border-radius: 100%;
          background-color: $gray-666;
        }
      }

      .btn-sm {
        margin-left: 10px;
      }
    }

    .table {
      margin-bottom: 20px;

      &.v-middle {
        td {
          vertical-align: middle;
        }
      }

      td {
        vertical-align: top;
      }

      .tit {
        font-weight: 500;
      }

      .img {
        display: block;
        text-align: center;

        img {
          display: inline-block;
        }
      }

      .des {
        display: block;
        line-height: 20px;
        color: #5669C8;
      }

      small {
        display: block;
        font-size: 15px;
        color: #5669C8;
      }

      &.table-type-2 {
        td {
          border-right: 0;

          .link {
            display: flex;
            align-items: center;
            text-align: left;
            font-size: 15px;

            img {
              width: 35px;
              height: auto;
              margin-right: 5px;
            }
          }

          &:last-child {
            border-right: 1px solid $gray-eee;
          }
        }
      }
    }
  }

  &.popup-service-pause {
    width: $popup-sm-width;
    height: auto;
    padding: 40px 30px 50px;

    .popup-head {
      margin-bottom: 0;

      .btn-popup-close {
        position: absolute;
        top: -22px;
        right: -9px;
        width: 20px;
        height: 20px;
        padding: 0;
      }
    }
  }

  &.popup-form {
    .login-contents {
      padding-top: 0;
    }
  }
}

.popup-service-pause {
  width: 100%;
  height: calc(100dvh - 40px);
  padding: 30px;
  overflow-y: auto;

  .popup-head {
    display: flex;
    justify-content: center;
    border-bottom: 1px solid $gray-eee;
    padding-bottom: 10px;
    margin-bottom: 20px;

    .title {
      font-size: 24px;
      line-height: 24px;
      font-weight: bold;
      letter-spacing: -1.2px;
      text-align: center;
    }
  }

  .popup-body {
    .error-content {
      .txt {
        font-size: 15px;
        line-height: 24px;
        letter-spacing: -0.75px;
        text-align: center;
        color: $gray-666;
        margin-bottom: 16px;
      }

      .img {
        width: 47px;
        margin: 0 auto 18px;

        img {
          width: 100%;
          height: auto;
          object-fit: cover;
        }
      }

      .info {
        padding: 20px 20px 15px;
        border-radius: 3px;
        border: solid 1px #ddd;
        background-color: #f6f6f6;
        margin-bottom: 20px;
        text-align: center;

        .tit {
          font-size: 16px;
          font-weight: bold;
          line-height: 16px;
          letter-spacing: -0.8px;
          text-align: center;
          color: $gray-000;
          margin-bottom: 10px;
        }

        .date {
          font-size: 18px;
          font-weight: 500;
          line-height: 24px;
          letter-spacing: -0.9px;
          color: $point;
        }
      }

      .txt-bottom {
        font-size: 15px;
        line-height: 15px;
        letter-spacing: -0.75px;
        text-align: center;
        color: $gray-666;
      }

      .img-box {
        text-align: center;

        img {
          display: inline-block;
          margin: 0 auto;
          max-width: 100%;
          height: auto;
        }
      }
    }
  }

  .popup-foot {
    position: fixed;
    bottom: 0;
    width: 100%;
    left: 0;
  }

  .error-content-bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    background-color: #333;
    padding: 10px 20px;
    color: $white;

    .form-check input + span::before {
      background-color: $white;
      border-radius: 3px;
    }

    .btn-close {
      display: flex;
      align-items: center;
      color: $gray-eee;
      font-size: 13px;
      letter-spacing: -0.65px;
      text-align: right;
      gap: 5px;
    }
  }

  .error-info {
    padding: 30px;
    border-radius: 3px;
    border: solid 1px #e4e8ee;
    background-color: #fff;
    min-height: 280px;

    .text {
      font-size: 15px;
      line-height: 24px;
      letter-spacing: -0.75px;
      color: $gray-666;
    }
  }

  &.popup-service-info {
    background-color: #f4f7fc;

    .popup-head {
      border-bottom: 0;
      padding-bottom: 0;
    }
  }
}

@media (max-width: $breakpoint-xlarge) {
  .popup {
    &.popup-lg {
      width: calc(100% - 40px);
    }

    &.popup-xl {
      width: calc(100% - 40px);
    }

    &.popup-terms {
      .form-group {
        .form-select {
          width: 100%;
        }
      }
    }
  }
}

@media (max-width: 768px) {
  .popup {
    width: calc(100% - 40px);
  }
}

@media (max-width: 540px) {
  .popup {
    padding: 20px 20px 30px;

    &.popup-sm {
      width: calc(100% - 40px);
    }

    &.popup-motp {
      .popup-text {
        br {
          display: none;
        }
      }
    }
  }
}