/*
  Tabs
*/

@use "variables" as *;
$tab-line-color: #666666;
$tab-line-point-color: #222222;

.tab-container {
  .tabs-box {
    margin-bottom: 20px;

    .tabs {
      display: flex;
      gap: 6px;

      .item {
        position: relative;
        flex: 1;
        height: 50px;
        background-color: $white;
        transition: .3s;
        word-break: keep-all;

        &::before {
          content: '';
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          height: 2px;
          background-color: $tab-line-color;
        }

        &::after {
          content: '';
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          height: 4px;
          background-color: $tab-line-point-color;
          transition: .3s;
          opacity: 0;
        }

        .tab-button {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 100%;
          height: 100%;
          font-size: 18px;
          font-weight: 500;
          letter-spacing: -0.45px;
          color: $gray-666;
          padding: 0 10px;
          text-align: center;
          transition: .3s;
        }

        &.on, &:hover {
          .tab-button {
            //font-size: 30px;
            font-weight: bold;
            color: $tab-line-point-color;
          }

          &::before {
            content: none;
          }

          &::after {
            opacity: 1;
          }
        }
      }
    }
  }

  .tab-content {
    .tab-panel {
      display: none;

      &.on {
        display: block;
      }
    }
  }
}

